// В этом файле должны быть стили для БЭМ-блока page-header, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.page-header {

  $block-name:                &; // #{$block-name}__element

  &__inner {
    padding-left: 20px;
    padding-right: 20px;

    @media (min-width: $screen-sm) {
      margin: auto;
      padding-left: 14px;
      padding-right: 14px;
    }

    @media (min-width: $screen-lg) {
      display: flex;
      flex-direction: column-reverse;
      width: 1140px;


    }
  }

  &__main-line {

    @media (min-width: $screen-sm) {
      display: flex;
    }
  }

  &__second-line {
    display: none;

    @media (min-width: $screen-lg) {
      display: flex;
      border-bottom: 1px solid $main-color--lighten;
      justify-content: space-between;
      padding-top: 13px;
      padding-bottom: 13px;

    }
  }

  &__opened {
    color: $gray;

    svg {
      display: inline-block;
      vertical-align: -0.05em;
      margin-right: 5px;
      fill: $main-color;
    }
  }

  &__addresses {
    display: none;
    list-style: none;

    @media (min-width: $screen-sm) {
      display: flex;
      flex-direction: row;
    }

  }

  .addresses {

    &__city {
      color: $gray;
    }

    &__phone {
      font-size: $font-size+2;
      font-weight: bold;
      text-decoration: none;
      color: #000;
    }

    &__item-left {
      position: relative;
      padding-right: 37px;
      padding-left: 23px;

      svg {
        position: absolute;
        left: 0;
      }
    }

    &__item-right {
      position: relative;
      padding-left: 23px;

      svg {
        position: absolute;
        left: 0;
      }
    }

  }

  &__email {
    color: $gray;
    text-decoration: none;

    svg {
      display: inline-block;
      vertical-align: -0.15em;
      margin-right: 5px;
    }
  }

  &__email-text {
    text-decoration: underline;
  }


  &__btn-wrap {
    display: none;

    @media (min-width: $screen-lg) {
      margin-top: 25px;
      margin-bottom: 33px;
      margin-left: 80px;
      display: block;
    }

  }

  &__logo-wrap {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    padding: 15px 0;
    border-bottom: 1px solid $main-color--lighten;
    margin-right: auto;

    @media (min-width: $screen-sm) {
      border-bottom: none;
      margin-bottom: 0;
      margin-right: auto;
      padding-top: 20px;
      padding-bottom: 20px;
      flex-direction: row;
      justify-content: flex-start;
    }
  }

  .burger {

    @media (min-width: $screen-lg) {
      display: none;
    }
}

  &__logo-wrap-im {

    @media (min-width: $screen-sm) {
      width: 177px;
      height: 55px;
    }

    @media (min-width: $screen-lg) {
      width: 200px;
      height: 60px;
      margin-top: -5px;
    }
  }
}
